#header{display:flex;justify-content:center;align-items:center;height:15em}#header .containerText{display:flex;flex-direction:column;align-items:center}#header .containerText h1{color:#333333;font-size:3em;font-style:italic;font-weight:600}#header svg{margin:0 1em;height:10em;width:10em}.content{background:#effcef;box-shadow:0px -0.09em 0.3em rgba(0,0,0,0.6)}.content .container{margin:auto;width:65vw;padding:2em 0;display:flex}.content .container .textContainer{display:flex;flex-direction:column;align-items:center;width:70%}.content .container .textContainer h2{align-self:flex-start;font-size:2em}.content .container .textContainer p{padding-right:1em;font-size:1.3em;text-align:justify}.content .container button{cursor:pointer;padding:0.4em 0.6em;margin-top:1.5em;width:8em;font-size:1.2em;background:#333333;color:#f9f9f9;border:none;border-radius:10px}.content .container button:hover{opacity:90%}.content .container svg{width:13em;height:13em;margin:0 2em}.charlie{position:relative;font-size:1.5em;height:1em;border:0;-webkit-filter:drop-shadow(rgba(0,0,0,0.75) 1px 3px 5px)}.charlie:before,.charlie:after{display:block;position:absolute;left:0;right:0;background-size:1em 100%;height:inherit}.charlie:before{content:'';background-image:linear-gradient(315deg, rgba(0,0,0,0.75) 25%, transparent 25%),linear-gradient(45deg, rgba(0,0,0,0.75) 25%, transparent 25%);background-position:50%;top:-0.5em}.charlie:after{content:'';background-image:linear-gradient(135deg, rgba(0,0,0,0.75) 25%, transparent 25%),linear-gradient(225deg, rgba(0,0,0,0.75) 25%, transparent 25%);background-position:-webkit-calc(50% - 0.5em);top:0.5em}
